body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@font-face {
  font-family: "futurist";
  src: url(/profile/static/media/ForgottenFuturistCollegeRegular.0dae820b.ttf)
}

@font-face {
  font-family: "FuturaRegular";
  src: url(/profile/static/media/FuturaTEERegular.6901c65d.ttf)
}

.App {
  text-align: center;
  font-family: futurist;
}

@media only screen and (min-width: 768px) {
  .container {
    width: 100%;
    height: 100vh;
    display: flex;
  }
  .left, .right {
    height: 100%;
    width: 50%;
  }
  .left {
    display: flex;
    justify-content: center;
    flex-direction: column;
    color: #0038A8;
  }
  .right {
    background: #0038A8;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .name {
    display: flex;
    flex-direction: column;
  }
  .name > .handle {
    font-size: 80px;
  }
  .name > .tagline {
    font-size: 20px;
  }
  .name > .title {
    font-size: 25px;
  }
  .image > img {
    width: 200px;
    height: 200px;
    border-radius: 50%; 
  }
  .image .name {
    display: none;
  }
  .links > div > span {
    margin: 0px 10px;
    font-size: 20px;
    cursor: pointer;
  }

  .links > div {
    /* margin: 30px 20px; */
  }

  .links > div > span.selected {
    text-decoration: underline;
  }

  .links > div > span:hover {
    text-decoration: underline;
  }

  .hamburg, .mobile-links {
    display: none;
  }
  .profile > .text {
    font-size: 30px;
    max-height: 600px;
    overflow: auto;
    min-width: 95%;
    padding: 20px 0;
  }
}

@media only screen and (max-width: 767px) {
  .container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
  }
  .left {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    color: #0038A8;
    width: 100%;
    height: 70px;
    flex-wrap: wrap;
  }
  hr {
    display: none;
  }
  .right {
    background: #0038A8;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .name {
    display: none;
  }
  .image .name {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-left: 10px;
    justify-self: flex-start;
  }
  .name > .handle {
    font-size: 50px;
    text-align: left;
  }
  .name > .title {
    font-size: 15px;
  }
  .image {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .image > img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: 10px; 
  }
  .links {
    display: none;
    font-family: 'FuturaRegular';
  }
  .name > .tagline {
    display: none;
  }
  .hamburg {
    width: 40px;
    height: 40px;
    border: 1px solid #0038A8;
    margin-right: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 40px;
  }
  .mobile-links {
    width: 100%;
    z-index: 99;
    background: #ffffff;
    height: 100px;
    padding-top: 20px; 
  }
  .mobile-links > div > span {
    margin: 30px 20px;
    font-size: 20px;
    cursor: pointer;
  }

  .mobile-links > div {
    margin: 5px 20px;
  }

  .mobile-links > div > span:hover, .links > div > span:hover {
    text-decoration: underline;
  }

  .mobile-links > div > span.selected {
    text-decoration: underline;
  }

  .profile > .text {
    font-size: 22px;
    overflow: auto;
    min-width: 90%;
    max-height: 600px;
    padding: 20px 0;
  }
}

.profile {
  color: #ffffff;
  width: 80%;
  height: 90%;
  margin: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile > .heading {
  font-size: 40px;
}

.links > div > span > a, .mobile-links > div > span > a {
  text-decoration: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.skills {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.skill {
  height: 150px;
  width: 150px;
  margin: 10px auto;
  display: flex;
  flex-direction: column;
  border-radius: 5px; 
  background: #ffffff;
  color: #0038A8;
  justify-content: space-around;
  cursor: pointer;
  position: relative;
  border: 1px solid #ffffff;
}

.skill > .img {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  width: 100%;
  height: 80%;
  margin: auto;
}

.skill > .details {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  display: flex;
  flex-direction: column;
  font-size: 18px;
  height: 20%;
  width: 100%;
  align-items: center;
  justify-content: center;
  text-transform: capitalize;
}

.overlay {
  background: #ffffff;
  opacity: 1;
  width: 150px;
  height: 80%;
  position: absolute;
  z-index: 99999;
  border-radius: 5px;
  top: 0;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  transition: height 1s;
  font-size: 16px;
  flex-direction: column;
  align-items: center;
  justify-items: center;
}

.overlay:hover {
  height: 80%;
}

.animate-skill {
  -webkit-animation: skill 0.3s ease 1 alternate;
          animation: skill 0.3s ease 1 alternate;
}

@-webkit-keyframes skill {
  0% { width: 150px; height: 0px; }
  100% { width: 150px; height: 80%; }
}

@keyframes skill {
  0% { width: 150px; height: 0px; }
  100% { width: 150px; height: 80%; }
}

.proficiency {
  margin: auto;
}

.experience {
  margin: auto;
}

.proficiency > .title, .experience > .title, .projects > .title {
  font-size: 18px;
  width: 100%;
  height: 100px;
}

.projects {
  margin: auto;
}

.techs {
  font-size: 14px;
}
.project-logo {
  border: 1px solid #ffffff;
  width: 120px;
  height: 120px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 60px;
  color: #ffffff;
  background: #0038A8;
  font-size: 80px;
}

.tech-container, .url {
  width: 96%;
  overflow: auto;
  word-wrap: break-word;
  font-size: 14px !important;
}

